import React from 'react';
import { Link } from 'react-router-dom';
import styles from './filtro.module.css';
import Topnav from '../topnav/topnav';

const Filtro = () => {
  const genres = [
    { name: 'bass', path: '/playlist/top-urbano' },
    { name: 'Pop', path: '/playlist/club-urbano' },
    { name: 'Jazz', path: '/playlist/calling' },
    { name: 'Hip-Hop', path: '/playlist/hiphop' },
    { name: 'Clásica', path: '/playlist/clasica' },
    { name: 'Reggae', path: '/playlist/reggae' },
    { name: 'Blues', path: '/playlist/blues' },
    { name: 'Electrónica', path: '/playlist/electronica' },
  ];

  return (
    <section>
      <Topnav />

      <div className={styles.gridContainer}>
        {genres.map((genre, index) => (
          <Link to={genre.path} key={index} className={styles.gridItem}>
            <div className={styles.gridContent}>{genre.name}</div>
          </Link>
        ))}
      </div>
    </section>
  );
};

export default Filtro;



